.divider-container {
    background: rgba(255, 255, 255, 0.5);
    width: 100%;
  }
  
  .divider {
    opacity: 0.5;
    width: 80%;
    margin: auto;
  }
  
  .divider img {
    width: 100%;
  }

.mobile-divider {
  margin: auto;
  width: 80%;
}

.mobile-divider img {
  width: 100%;
  opacity: 0.8;
}

@media (min-width: 600px) {

  .mobile-divider img {
    opacity: 0.5;
  }
}
