/* Navbar Logo Styling */
.desktop-nav {
    display: none;
}

.logo-container {
    position: relative;
    width: 100%;
    padding: 20px 0px 30px;
    background: white;
    z-index: 1000;
}

.logo-container h4 {
    font-family: "Times New Roman", serif;
    font-weight: 900;
    font-style: italic;
    font-size: 10px;
    margin: 10px auto 0px;
    color: #2c426f;
    text-align: center;
}

.logo {
    width: 250px;
    margin: auto;
    transition: 200ms;
}

.logo:hover {
    opacity: 80%;
}

.logo img {
    width: 100%;
}

@media (min-width: 600px) {
    .logo {
        width: 300px;
    }

    .logo-container h4 {
        font-size: 14px;
    }
}
