.btn-container {
  text-align: center;
  padding-top: 60px;
  width: 100%;
}

.default-btn {
  padding: 20px 80px;
  font-size: 20px;
  border-radius: 6px;
}

.light-btn {
  background: white;
  color: #3c485e;
  border: none;
  box-shadow: 0px 0px 0px 2px #3c485e, 0px 2px 4px black;
  transition: 300ms;
}

.light-btn:hover {
  cursor: pointer;
  background: #3c485e;
  color: white;
  box-shadow: 0px 0px 0px 8px #3c485e, 0px 2px 16px black;
}

.dark-btn {
  background: white;
  color: #3c485e;
  border: none;
  box-shadow: 0px 0px 0px 2px #3c485e, 0px 2px 4px black;
  transition: 300ms;
}

.dark-btn:hover {
  cursor: pointer;
  background: #3c485e;
  color: white;
  box-shadow: 0px 0px 0px 8px #3c485e, 0px 2px 16px black;
}

@media (min-width: 800px) {  
  .default-btn {
    padding: 20px 80px;
    font-size: 16px;
  }

  .light-btn {
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    box-shadow: 0px 0px 0px 2px white, 0px 2px 4px black;
    transition: 300ms;
  }
  
  .light-btn:hover {
    cursor: pointer;
    background: white;
    color: rgba(0, 6, 17, 0.95);
    box-shadow: 0px 0px 0px 8px white, 0px 2px 4px 10px black;
  }
  
  .dark-btn {
    background: rgba(255, 255, 255, 0.5);
    color: #3c485e;
    border: none;
    box-shadow: 0px 0px 0px 2px #3c485e, 0px 2px 4px #888;
    transition: 300ms;
  }
  
  .dark-btn:hover {
    cursor: pointer;
    background: #3c485e;
    color: white;
    box-shadow: 0px 0px 0px 8px #3c485e, 0px 2px 4px 10px #888;
  }
}

@media (min-width: 1000px) {
  .default-btn {
    padding: 20px 80px;
    font-size: 20px;
  }
}