.mobile-form-container form {
  padding-top: 20px;
  text-align: center;
}

.mobile-form-container {
  max-width: 500px;
  margin: auto;
}

input {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  border-bottom: 1px solid #888;
  padding: 5px;
  margin-top: 20px;
  font-size: 18px;
  width: 80%;
  max-width: 400px;
}

textarea {
  background: rgba(255, 255, 255, 0.5);
  resize: none;
  font-family: "Roboto", sans-serif;
  width: 90%;
  max-width: 400px;
  border: 1px solid #888;
  border-radius: 6px;
  padding: 10px;
  margin-top: 40px;
  font-size: 18px;
}


.form-container {
  text-align: center;
  width: 60%;
  max-width: 500px;
  margin: auto;
}

.form-container p {
  font-size: 20px;
}

.form-container form {
  padding-top: 20px;
}

.mobile-only {
  display: block;
}

.desktop-only {
  display: none;
}

@media (min-width: 800px) {
  .mobile-only {
    display: none;
  }

  .desktop-only {
    display: block;
  }
}

