.image-overlay {
    width: 100%;
    background: rgba(0, 6, 17, 0.7);
  }

  .image-container {
    background-attachment: scroll;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 300px;
  }

  @media (min-width: 600px) {
    .image-container {
      height: 350px;
    }
  }
  
  .window-title {
    text-align: center;
    padding: 60px 20px 40px;
    position: relative;
    z-index: 1000;
  }
  
  .window-title h2 {
    color: white;
    text-shadow: 0px 2px 4px #000000ee;
    font-size: 36px;
    font-style: italic;
  }
  
  .window-content {
    text-align: left;
    width: 85%;
    margin: auto;
    padding: 0px 0px 60px;
  }
  
  .window-content p {
    text-align: left;
    color: white;
    position: relative;
    z-index: 1000;
    font-size: 16px;
    line-height: 40px; 
  }
  
  /* Text Left Styling */
  .text-left-container {
    background: rgba(0, 6, 17, 0.95);
    width: 50%;
  }
  
  /* Small Text Left Styling */
  .small-text-left-container {
    background: rgba(0, 6, 17, 0.95);
    width: 30%;
    min-width: 500px;
  }
  
  /* Text Right Styling */
  .text-right-container {
    background: rgba(0, 6, 17, 0.95);
    width: 50%;
    margin: 0px;
    margin-left: auto;
  }

  @media (min-width: 800px) {
    .image-container {
      background-attachment: fixed;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 100%;
    }
  }
