.footer-container {
    background-image: url("../../images/background-blue.png");
    background-attachment: scroll;
    position: relative;
    width: 100%;
}

.footer-overlay {
    padding-top: 50px;
    background: rgba(0, 0, 0, 0.2);
}

.links-container {
    padding: 50px 50px 80px;
    width: 70%;
    margin: auto;
}

.footer-link {
    margin-bottom: 30px;
}

.footer-link a {
    text-decoration: none;
    color: white;
    opacity: 0.8;
}

.footer-link a:hover {
    opacity: 1;
    font-weight: bold;
}

.copyright p {
    text-align: center;
    font-size: 12px;
    color: white;
    opacity: 0.5;
    padding: 10px;
}

.footer-active a {
    opacity: 1;
    letter-spacing: 2px;
    font-weight: bold;
}
