.image-container {
    background-attachment: scroll;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .page-overlay {
    width: 100%;
    background: rgba(255, 255, 255, 0.3);
  }
  
  .page-container {
    margin: auto;
    width: 90%;
  }
  
  .no-title {
    padding-top: 60px;
  }
  
  .page-title {
    text-align: center;
    padding: 60px 0px 30px;
    position: relative;
    z-index: 1000;
  }
  
  .page-title h2 {
    color: #3c485e;
    font-size: 36px;
    font-style: italic;
  }
  
  .page-content {
    width: 100%;
    margin: auto;
    padding: 20px 0px 60px;
  }
  
  .page-content p {
    text-align: left;
    color: #333333;
    position: relative;
    z-index: 1000;
    font-size: 18px;
    line-height: 40px;
  }

  @media (min-width: 600px) {
    .page-content p {
      font-size: 18px;
      line-height: 50px;
    }
  }

  @media (min-width: 800px) {
    .page-container {
        width: 80%;
      }
    
      .no-title {
        padding-top: 60px;
      }
    
      .page-title {
        padding: 60px 0px 20px;
      }
    
      .page-title h2 {
        font-size: 40px;
      }
    
      .page-content {
        padding-bottom: 60px;
      }
    
      .page-content p {
        text-align: center;
      }
  }

  @media (min-width: 1300px) {
    .page-container {
        width: 60%;
      }
  }

  @media (min-width: 1600px) {
    .page-container {
        width: 50%;
      }
    
      .no-title {
        padding-top: 60px;
      }
    
      .page-title {
        padding: 100px 0px 40px;
      }
    
      .page-title h2 {
        font-size: 48px;
      }
    
      .page-content {
        padding-bottom: 100px;
      }
  }