.desktop-nav {
    display: none;
}

/* Navbar Styling */
.navbar-container {
    position: relative;
    background: #95a0a9;
    text-align: center;
    z-index: 1000;
}

.nav-items-container {
    display: inline;
    margin: auto;
    padding: 15px 0px;
    background: white;
    border-left: 2px solid white;
    border-right: 2px solid white;
}

.nav-item {
    display: inline-block;
    text-align: center;
    padding: 15px 0px;
}

.nav-item a {
    position: relative;
    background: #dadee2;
    color: #333;
    text-decoration: none;
    padding: 15px 20px;
    transition: 200ms ease-in-out;
    margin: 0px 2px;
}

.nav-item a:hover {
    padding: 20px;
    background: #2c426f;
    color: white;
    box-shadow: 0px 2px 4px #000000aa;
}

.active a {
    background: #2c426f;
    color: white;
}

.active a:hover {
    padding: 15px 20px;
    box-shadow: none;
    cursor: default;
}

@media (min-width: 1100px) {
    .desktop-nav {
        display: block;
    }
}