.hero-container {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 600px;
}
  
.title {
    position: absolute;
    text-align: center;
    transform: translateY(200px);
    text-shadow: 0px 2px 4px #000000ee;
    width: 100%;
    z-index: 20;
}

.hero-container:after {
  content: " ";
  z-index: 10;
  display: block;
  position: relative;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  pointer-events: none;
}

.title {
    position: absolute;
    text-align: center;
    transform: translateY(200px);
    text-shadow: 2px 3px 0px 2px #000000;
    width: 100%;
    z-index: 20;
}

.title h1 {
    color: white;
    letter-spacing: 3px;
    padding: 30px;
    font-size: 56px;
}

.hsq-title h1 {
    font-size: 32px;
}

.title h4 {
  color: white;
  margin: 0;
  font-style: italic;
  font-size: 20px;
  letter-spacing: 2px;
}

@media (min-width: 900px) {
    .title h1 {
        font-size: 64px;
    }

    .hsq-title h1 {
        font-size: 36px;
    }
}

@media (min-width: 1000px) {
    .title h4 {
        font-size: 24px;
    }

    .hsq-title h1 {
        font-size: 42px;
    }
}

@media (min-width: 1100px) {

    .hsq-title h1 {
        font-size: 48px;
    }
}

@media (min-width: 1300px) {
    .title h4 {
        font-size: 28px;
    }

    .hsq-title h1 {
        font-size: 56px;
    }
}

@media (min-width: 1600px) {
    .title h1 {
        font-size: 72px;
    }

    .hsq-title h1 {
        font-size: 60px;
    }

    .title h4 {
        font-size: 32px;
    }

    .hero-container {
        height: 700px;
    }
}